<script>
import { request } from '@/utils/request'
import { useUserStore } from '@/stores/user'

export default {
  data() {
    return {
      securityLevel: 60,
      phoneDisplay: '',
      hasPassword: false,
      hasPayPassword: false,
      isRealname: false,
      bankCardCount: 0
    }
  },
  onLoad() {
    this.loadSecurityInfo()
  },
  methods: {
    async loadSecurityInfo() {
      try {
        const userStore = useUserStore()
        const userInfo = userStore.userInfo
        
        if (userInfo.phone) {
          this.phoneDisplay = userInfo.phone.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')
        }
        
        // ËÆ°ÁÆóÂÆâÂÖ®Á≠âÁ∫ß
        let level = 0
        if (userInfo.phone) level += 20
        if (userInfo.password) {
          level += 20
          this.hasPassword = true
        }
        if (userInfo.payPassword) {
          level += 20
          this.hasPayPassword = true
        }
        if (userInfo.isRealname) {
          level += 20
          this.isRealname = true
        }
        if (userInfo.bankCardCount > 0) {
          level += 20
          this.bankCardCount = userInfo.bankCardCount
        }
        
        this.securityLevel = level
      } catch (e) {
        console.error('Âä†ËΩΩÂÆâÂÖ®‰ø°ÊÅØÂ§±Ë¥•', e)
      }
    },
    getLevelText(level) {
      if (level >= 80) return 'ÂÆâÂÖ®Á≠âÁ∫ßÔºöÈ´ò'
      if (level >= 60) return 'ÂÆâÂÖ®Á≠âÁ∫ßÔºö‰∏≠'
      return 'ÂÆâÂÖ®Á≠âÁ∫ßÔºö‰Ωé'
    },
    goPage(page) {
      uni.showToast({ title: 'ÂäüËÉΩÂºÄÂèë‰∏≠', icon: 'none' })
    }
  }
}
</script>

